* {
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif
}

body {
    padding: 0;
    margin: 0;
    width: 100vw;
    height: 100vh;
    background-color: blanchedalmond;
    color: #777;
    display: flex;
    justify-content: center;
}
.App {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90vw;
    flex-direction: column;
}
.serch-section {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 200px;
    flex-direction: column;
}
.surprise{
    
    
    background-color: #ececf1;
    color:black;
    border-radius: 5px;
    font-weight:800px;
    margin: 0 0 2px 5px;
    padding: 5px 10px;
}
.input-container {
    display: flex;
    width: 100%;
    border-radius: 6px;
    overflow: hidden;
    box-shadow: rgba(0, 0, 82, 0.15) 0px 4px 4px;
}
.input-container input {
    width: 90%;
    padding: 10px 14px;
    border: none;
    outline: none;
    font-size: 15px;
    color: #777;
    box-sizing: border-box;
}
.input-container button {
    width: 10%;
    padding: 10px 14px;
    border: none;
    outline: none;
    font-size: 15px;
    color: #777;
    box-sizing: border-box;
    background-color: #ececf1;
    cursor: pointer;
}
.input-container button :active {
    background-color: #cacaca;
}
.image-section {
    width:100%;
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    justify-content: space-between;
}
.image-section img {
   max-width: 33%;
    min-width: 250px;
    margin: 1px;
    flex-grow: 1;
   
}

@media (max-width: 556px){
    .image-section img {
        max-width: 100%;
    
}
}
.extra-info {
    text-align: center;
}
.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color:rgba(0,0,0,0.15);
    overflow: hidden;
display: flex;
justify-content: center;
align-items: center;
z-index: 10;
}
.modal {
    position: relative;
    z-index: 100;
    background-color: #ffff;
    padding: 10px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;

}
.modal button {
    width: 100%;
    padding: 20px ;
    border: none;
    cursor: pointer;
}
.modal button :active {
    background-color: #fafafc;

}

.img-container {
    height: 256px;
    width: 256px;
    overflow: hidden;
}